@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 10px;
    text-decoration: none;
    list-style: none;
    font-family: 'Red Hat Display', sans-serif;
}

body.js-mobile-menu-open {
    overflow: hidden;
    height:100%;
}

@media all and (min-width: 1024px){
    *{
        font-size: 16px;
    }
}
 .nonactive_burger, .cv, .outer_stripes{
    display: none;
}
/* ACTIVE MOBILE MENU - START */
.active_burger {
    position: absolute;
    top: 73.9px;
    left: 0;
    height: calc(100vh - 73.9px);
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 99;
    line-height: calc((100vh - 73.9px)/4);
    text-align: center;
    background-color: #fff;
    -webkit-transform: translate(-150%, -150%);
            transform: translate(-150%, -150%);
    transition: -webkit-transform .5s linear;
    transition: transform .5s linear;
    transition: transform .5s linear, -webkit-transform .5s linear;
}

.active_burger a{
    display: block;
    width: 100vw;
    font-size: 1.6rem;
    font-weight: 700;
}

.active_burger a:hover{
    color: #F6C004;
}

.active_burger{
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
}
/* ACTIVE MOBILE MENU - END */

.homepage{
    /* width: 100vw; */
    height: 100vh;
    padding: 0 2rem;
}

.homepage_wrapper{
    height: 90vh;
}

.logo{
    cursor: pointer;
}

.burger_mobile div {
    height: 0.3rem;
    background-color: #F6C004;
    margin: 0.7rem 0 0.7rem auto;
    border-radius: 8px;
    transition: all .3s ease;
}

.burger_mobile div:nth-of-type(1){
    width: 2.2rem;
}

.burger_mobile div:nth-of-type(2){
    width: 1.7rem;
}

.burger_mobile div:nth-of-type(3){
    width: 2.5rem;
}

.top_bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    padding-top: 1rem;
}

.burger_language_cv_container, .language{
    display: flex;
    align-items: center;
}

.language img {
    height: 25px;
    padding: 0 2rem;
}

.main_txt{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.first_txt p, .second_txt p:nth-of-type(1), .third_txt p {
    font-size: 4rem;
    font-weight: bold;
}

.second_txt{
    display: flex;
    align-items: center;
}

.yellow_txt {
    font-size: 1.7rem;
    color: #F6C004;
    max-width: 100px;
    margin-left: 2rem;
    letter-spacing: .7px;
}

.go_down_btn{
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: #F6C004;
    color: #fff;
    margin-bottom: 5vh;
    cursor: pointer;
    transition: background-color .2s;
}

.RiArrowDropDownLine{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 3.5rem;
}

.go_down_btn:active{
    color: #F6C004;
    border: 2px solid #F6C004;
    background-color: #fff;
}

@media all and (min-width: 425px){
    .first_txt p, .second_txt p:nth-of-type(1), .third_txt p {
        font-size: 5rem;
    }

    .yellow_txt {
        font-size: 2rem;
    }
}

@media all and (min-width: 768px){
    .first_txt p, .second_txt p:nth-of-type(1), .third_txt p {
        font-size: 6rem;
    }

    .yellow_txt {
        font-size: 2.3rem;
    }
}

@media all and (min-width: 1024px){
    .top_bar ul, .cv, .outer_stripes{
        display: block;
    }

    .burger_mobile{
        display: none;
    }

    .language img {
        height: 35px;
    }

    .first_txt p, .second_txt p:nth-of-type(1), .third_txt p {
        font-size: 4rem;
    }

    .yellow_txt {
        font-size: 1.7rem;
    }

    .go_down_btn{
        width: 3rem;
        height: 3rem;
        margin-bottom: 7vh;
        z-index: 99;
    }

    .go_down_btn:hover{
        color: #F6C004;
        border: 2px solid #F6C004;
        background-color: #fff;
    }
    
    .RiArrowDropDownLine{
        font-size: 2.5rem;
    }

    .homepage{
        max-width: 1440px;
        margin: 0 auto;
    }

    .top_bar ul{
        display: flex;
    }

    .top_bar ul li a{
        display: block;
        color: #818498;
        padding: 1rem 2rem;
        transition: .2s;
        cursor: pointer;
    }

    .top_bar ul li a:hover{
        color: #F6C004;
    }

    .cv{
        color: #F6C004;
        border: 2px solid #F6C004;
        padding: .5rem 1rem;
        border-radius: 20px;
        cursor: pointer;
        transition: .2s;
    }

    .cv:hover{
        color: #fff;
        background-color: #F6C004;
    }

    .outer_stripes{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        position: absolute;
        bottom: 0;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        width: 100%;
        max-width: 1440px;
        padding: 0 2rem;
    }

    .outer_stripes_left, .outer_stripes_right{
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 30px;
    }

    .line{
        width: 1px;
        height: 100px;
        background-color: #818498;
        margin-top: 20px;
    }

    .github_icon, .linkedin_icon{
        font-size: 1.5rem;
        color: #818498;
        transition: .2s;
    }

    .github_icon:hover, .linkedin_icon:hover{
        color: #F6C004;
    }

    .outer_stripes_left a{
        padding: 10px 0;
    }

    .outer_stripes_right p {
        color: #818498;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        margin-bottom: 100px;
    }
}

@media all and (min-width: 1300px){
    .first_txt p, .second_txt p:nth-of-type(1), .third_txt p {
        font-size: 5rem;
    }

    .yellow_txt {
        font-size: 2rem;
    }

    .go_down_btn{
        width: 3.5rem;
        height: 3.5rem;
    }
    
    .RiArrowDropDownLine{
        font-size: 3rem;
    }
}

.about_me_wrapper{
    display: flex;
    /* justify-content: space-between; */
    justify-content: center;
    flex-wrap: wrap;
    padding: 5vh 2rem;
}

.left_area_am h2, .left_area_am h2 span{
    font-size: 4rem;
    margin-bottom: 2rem;
    /* text-align: center; */
}

.left_area_am h2 span{
    color: #F6C004;
}

.left_area_am{
    max-width: 380px;
    /* margin: 0 auto; */
}

.left_area_txt_am{
    font-size: 1.6rem;
    margin-bottom: 2rem;
    max-width: 380px;
    line-height: 1.5;
}

.more_info{
    position: relative;
    overflow: hidden;
    font-weight: bold;
}

.more_info a {
    color: #000;
    font-size: 1.6rem;
}

.more_info a::before{
    content: "";
    position: absolute;
    width: 100px;
    height: 2px;
    left: -100px;
    top: 95%;
    background: #F6C004;
    opacity: 0;
    transition: .2s linear;
}

.more_info a:hover::before{
    opacity: 1;
    left: 0;
}

.more_info_arrow{
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    font-size: 2rem;
    margin-left: 5px;
}

.right_area_am{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 4rem;
}

.photo_container{
    position: relative;
    height: calc(70vw + 30px);
    max-height: 380px;
    width: calc(70vw + 30px);
    max-width: 380px;
}

.photo_background{
    height: 70vw;
    max-height: 350px;
    width: 70vw;
    max-width: 350px;
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #F6C004;
    z-index: -1;
}

.my_photo img{
    width: 70vw;
    max-width: 350px;
}

@media all and (min-width: 1024px){
    .about_me_wrapper{
        max-width: 1440px;
        margin: 0 auto;
        padding: 10vh 2rem;
        justify-content: space-between;
    }

    .right_area_am{
        width: 50%;
    }

    .left_area_txt_am{
        font-size: 1.2rem;
        max-width: 450px;
    }

    .left_area_am{
        max-width: 450px;
    }

    .more_info a::before{
        width: 150px;
        left: -150px;
    }
}
.skills{
    max-width: 1440px;
    margin: 0 auto;
    padding: 5vh 2rem;
}

.skills_header h2, .skills_header h2 span{
    font-size: 4rem;
    margin-bottom: 2rem;
}

.skills_header h2 span{
    color: #F6C004;
}

.skills_subtitle p{
    font-size: 2rem;
    margin-bottom: 2rem;
    font-weight: 700;
}

.skills_second_container{
    padding-top: 4rem;
}

.first_container_technologies div p, .second_container_technologies div p{
    font-size: 1.2rem;
    margin-top: 1rem;
    font-weight: 700;
}

.skills_first_container{
    width: 100%;
}

.first_container_technologies, .second_container_technologies{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.first_container_technologies div, .second_container_technologies div{
