}


.slider_mobile div{
    text-align: center;
}

.slider_mobile div img{
    width: 90vw;
    max-width: 600px;
    margin: 2rem 0;
}

.projects_wrapper_img_0, .projects_wrapper_img_1, .projects_wrapper_img_2, .projects_wrapper_img_3, .projects_wrapper_img_4{
    position: relative;
}

.projects_img_0, .projects_img_1, .projects_img_2, .projects_img_3, .projects_img_4{
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.active_project_0, .active_project_1, .active_project_2, .active_project_3, .active_project_4{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90vw;
    max-width: 600px;
    height: 90vw;
    max-height: 600px;
    background-color: rgba(236, 236, 236, 0.5);
}

.nonactive_project{
    display: none;
}

.nonprojects_img{
    -webkit-filter: blur(0);
            filter: blur(0);
}

.active_project_0 div, .active_project_1 div, .active_project_2 div, .active_project_3 div, .active_project_4 div{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.active_project_0 div a, .active_project_1 div a, .active_project_2 div a, .active_project_3 div a, .active_project_4 div a{
