    }

    .technologies_icon{
        font-size: 3.5rem;
        margin: 5rem 1.5rem 0 0;
    }

    .link{
        margin-top: .5rem;
        font-weight: 700;
    }

    .link a{
        font-weight: normal;
        color: #818498;
    }

    .link a:hover{
        color: #F6C004;
        transition: .2s;
    }
}






